<template>
    <div class="container rebase-control-wrapper">
        <h1 class="text-light">Tables</h1>
        <div class="tabs">
            <div class="tab" 
                v-bind:class="{active: getActive(index)}" 
                v-on:click="setActiveTable(index)" 
                v-for="(table, index) in tables" :key="table.Id">
            <div><a>{{table.tableLabel}}</a></div>
            </div>
            <div class="tab new">
            <a href="#" class="rebase-button" data-rebase-action="tables.new">
                <img src="/img/add_circle_outline_24px.svg" alt="" class="nav-item-icon">
                <div class="modal-open">New base</div>
            </a>
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions, mapState } from 'vuex';
import { Action } from '../../store/types';

export default {
    name: 'NavbarTablesSelector',
    computed: {
        ...mapState({
            tables: state => state.user.tables
        })
    },
    methods: {
        ...mapActions({
            setActiveTable: Action.SET_ACTIVE_TABLE
        }),
        getActive(index) {
            return this.tables[index].active;
        }
    },
    created: function() {
        this.setActiveTable(0);
    }
}
</script>
<style scoped>
.tab {
    cursor: pointer;
}
</style>